import React, { useEffect, useState, useRef } from "react";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import * as XLSX from 'xlsx'; 
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  InputAdornment,
  Stack,
  TextField,
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Pagination,
  Paper,
  Select,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tabs,
  Typography,
  Autocomplete,
  Grid,
  Avatar,
  FormHelperText,
  Tooltip,
} from "@mui/material";
import { ToastContainer } from "react-toastify";
import Loader from "../../../common/loader/Loader";
import { payInWalletReport , PayInReportExcel } from "../../../api/login/Login";
import { useParams } from "react-router-dom";
import { message } from "antd";



const PayInWalletReport = () => {
     const {id } =  useParams()
     
     const today = new Date().toISOString().split("T")[0];
      const [page, setPage] = useState(1);
      const [rowsPerPage, setRowsPerPage] = useState(10);
      const [resetKey, setResetKey] = useState(false);
      const [totalData, setTotalData] = useState(0);
      const [loading, setLoading] = useState(false);
      const [data, setData] = useState([]);

        const [filters, setFilters] = useState({
          startDate: today,
          endDate: today,
          txn_id :'',
          order_id:''
        });

          const handleChangePage = (_, value) => {
            setPage(value);
          };
          const handleChangeRowsPerPage = (event) => {
            setRowsPerPage(Number(event.target.value));
            setPage(1);
          };
        
          const handleChange = (name, value) => {
            const onlyNumbers = /^[0-9\b]+$/;
        
            // Allow only numbers for numeric fields
        
            const numberFields = ["user_mobile"];
            if (
              numberFields.includes(name) &&
              value !== "" &&
              !onlyNumbers.test(value)
            ) {
              return;
            }
            setFilters((prev) => ({
              ...prev,
              [name]: value,
            }));
          };
        
          const handleSubmit = () => {
            setResetKey((pre) => !pre);
            // you can pass `filters` to your API or logic here
          };
        
          const handleReset = () => {
            setFilters({
              startDate: "",
              endDate: "",
              txn_id :'',
              order_id:'',
            });
        
            setPage(1);
            setRowsPerPage(10);
            setResetKey((pre) => !pre);
          };

            const getList = async()=> {
                   let body = {
                    page : page,
                    count : rowsPerPage,
                    start_date : filters.startDate,
                    end_date : filters.endDate,
                    txn_id :filters.txn_id,
                    order_id: filters?.order_id
                   }
                   if(id)
                   {
                    body.user_id = id
                   }
                try {
                   let res = await payInWalletReport(body) 
                   if(res?.data?.statusCode === 200)
                   {
                    setData(res?.data?.data?.wallet)
                    setTotalData(res?.data?.data?.total)
                   }
                   else {
                    setData([])
                    setTotalData(0)
                   }
                
                } catch (error) {
                    
                }
            }
        
          useEffect(() => {
              getList()
          }, [resetKey, page, rowsPerPage]);
        
          const totalPages = Math.ceil(totalData / rowsPerPage);

              const downloadExcel = async () => {
                    try {
                      setLoading(true);
                       let body = {
                         ...filters,
                       }
                      if(id)
                       {
                        body.user_id = id
                       }
                      const res = await PayInReportExcel(body);
                     
                      if (res?.data?.data?.wallet?.length > 0) {
                       const dataForExcel = res?.data?.data?.wallet?.map((item, index) => ({
      "Date / Time": item?.createdAt,
      "Order ID": item?.order_id,
      "Transaction ID": item?.txn_id,
      "Opening Balance": item?.o_bal,
      "Amount": item?.amount,
      "Closing Balance": item?.c_bal,
      "Transaction Type": item?.trans_type,
      "Type": item?.type,
      "Message": item?.message,
      "Updated At": item?.updatedAt,
    }));
                        const ws = XLSX.utils.json_to_sheet(dataForExcel);
                        const wb = XLSX.utils.book_new();
                        XLSX.utils.book_append_sheet(wb, ws, "Pay_In Report");
                        XLSX.writeFile(wb, `Pay_In_Report_${new Date().toLocaleDateString()}.xlsx`);
                      } else {
                        message.error('No data available to export!');
                      }
                    } catch (error) {
                      message.error('Failed to download Excel. Please try again.');
                    } finally {
                      setLoading(false);
                    }
                  };     
  return (
   <>
   
         {loading && <Loader />}

            <Paper
                 sx={{
                   padding: 3,
                   borderRadius: 2,
                   // border: "1px solid #ccc",
         
                   // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
                   boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
                 }}
               >
                 <Typography
                   variant="h6"
                   sx={{
                     fontWeight: "600",
                     fontSize: "24px",
                     color: "var(--bs-nav)",
                     textAlign: "center",
                   }}
                 >
                   Pay In Wallet
                 </Typography>
               </Paper>
         
               <div className="card">
                 <div className="card-header"> {/* <span>Filter</span> */}</div>
                 <div className="card-body">
                   <form>
                     <div className="form-row" style={{ alignItems: "end" }}>
                       <div className="form-group col-md-4">
                         <label>Start Date</label>
                         <input
                           type="date"
                           name="startDate"
                           className="form-control"
                           value={filters.startDate}
                           onChange={(e) => handleChange("startDate", e.target.value)}
                           max={today}
                         />
                       </div>
                       <div className="form-group col-md-4">
                         <label>End Date</label>
                         <input
                           type="date"
                           name="endDate"
                           className="form-control"
                           value={filters.endDate}
                           onChange={(e) => handleChange("endDate", e.target.value)}
                           max={today}
                         />
                       </div>
                       <div className="form-group col-md-4">
                         <label>Txn Id</label>
                         <input
                           type="text"
                           name="txn_id"
                           className="form-control"
                           value={filters.txn_id}
                           onChange={(e) => handleChange("txn_id", e.target.value)}
                          
                         />
                       </div>
                       <div className="form-group col-md-4">   
                       <label>Order Id</label>
                         <input
                           type="text"
                           name="order_id"
                           className="form-control"
                           value={filters.order_id}
                           onChange={(e) => handleChange("order_id", e.target.value)}
                          
                         />
                       </div>
                       <div className="form-group col-md-12 col-lg-12">
                         <div>
                           <button
                             type="button"
                             className="btn btn-primary mr-3"
                             onClick={handleSubmit}
                           >
                             Search
                           </button>
                           <button
                             type="button"
                             className="btn btn-light"
                             onClick={handleReset}
                           >
                             Reset
                           </button>
                         </div>
                       </div>
                     </div>
                   </form>
                 </div>
               </div>
         
               <Paper
                 sx={{
                   padding: 3,
                   borderRadius: 2,
                   // border: "1px solid #ccc",
                   marginTop: 4,
                   // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
                   boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
                 }}
               >
                 <Box display={"flex"} justifyContent={"flex-end"} columnGap={3}>
                   <Button
                     variant="contained"
                     color="nav"
                     sx={{
                       borderColor: "var(--bs-nav)", // Custom colors
         
                       backgroundColor: "var(--bs-nav)", // Custom colors
                       color: "white",
                     }}
                     onClick={downloadExcel}
                   >
                     Download Excel
                   </Button>
                
                 </Box>
         
                 <Box sx={{ padding: 1 }}>
                   <TableContainer component={Box} elevation={3}>
                     <Table>
                       <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
                         {/* {renderTableHeader()} */}
                         <TableRow>
                           <TableCell>
                             <strong>S.No.</strong>
                           </TableCell>
                            <TableCell sx={{minWidth:'180px'}}>
                             <strong>Date / Time</strong>
                           </TableCell>
         
                           <TableCell>
                             <strong>Order Id</strong>
                           </TableCell>
                           <TableCell>
                             <strong> Txn Id</strong>
                           </TableCell>
                           <TableCell>
                             <strong> Opening Balance</strong>
                           </TableCell>
                           <TableCell>
                             <strong> Amount</strong>
                           </TableCell>
                           <TableCell>
                             <strong> Closing Balance</strong>
                           </TableCell>
                           <TableCell>
                             <strong> TransType</strong>
                           </TableCell>
                           <TableCell>
                             <strong> Type</strong>
                           </TableCell>
                           <TableCell sx={{minWidth:'250px'}}>
                             <strong>Message</strong>
                           </TableCell>
                           {/* <TableCell>
                             <strong>Updated Date</strong>
                           </TableCell> */}
                          
                        
                         </TableRow>
                       </TableHead>
         
                       
                       <TableCell sx={{ color: "var(--bs-nav)" }}>
                       
                       </TableCell>
                       <TableBody>
                         {data?.map((item, index) => (
                           <TableRow key={index} hover>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {index + 1 + (page - 1) * rowsPerPage}
                             </TableCell>
                               <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.createdAt}
                             </TableCell>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.order_id}
                             </TableCell>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.txn_id}
                             </TableCell>
                             {/* <TableCell sx={{color:'var(--bs-nav)'}}>14</TableCell>
                              <TableCell sx={{color:'var(--bs-nav)'}}>15</TableCell> */}
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.o_bal}
                             </TableCell>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.amount}
                             </TableCell>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.c_bal}
                             </TableCell>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.trans_type}
                             </TableCell>
                             <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.type}
                             </TableCell>
                              <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.message}
                             </TableCell>
                              {/* <TableCell sx={{ color: "var(--bs-nav)" }}>
                               {item?.updatedAt}
                             </TableCell> */}
                           </TableRow>
                         ))}
                       </TableBody>
                     </Table>
                   </TableContainer>
         
                   {/* Pagination + Rows Per Page */}
                   <Box
                     display="flex"
                     justifyContent="space-between"
                     alignItems="center"
                     mt={2}
                     flexWrap="wrap"
                     gap={2}
                   >
                     <Box display="flex" alignItems="center" gap={2}>
                       <FormControl size="small" sx={{ width: "150px" }}>
                         <InputLabel>Rows per page</InputLabel>
                         <Select
                           value={rowsPerPage}
                           onChange={handleChangeRowsPerPage}
                           label="Rows per page"
                           sx={{
                             fontSize: "14px",
                             "& .MuiOutlinedInput-notchedOutline": {
                               borderColor: "#ccc",
                             },
                             "&:hover .MuiOutlinedInput-notchedOutline": {
                               borderColor: "var(--bs-primary1)",
                             },
                             "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                               borderColor: "var(--bs-primary1)",
                             },
                           }}
                         >
                           {[5, 10, 25, 50].map((num) => (
                             <MenuItem key={num} value={num}>
                               {num}
                             </MenuItem>
                           ))}
                         </Select>
                       </FormControl>
         
                       {/* Total Entries Count */}
                       <Typography
                         variant="body2"
                         sx={{ fontSize: "14px", color: "#555" }}
                       >
                         Total Entries: {totalData}
                       </Typography>
                     </Box>
         
                     <Pagination
                       count={totalPages}
                       page={page}
                       onChange={handleChangePage}
                       // color="primary"
                       shape="rounded"
                       showFirstButton
                       showLastButton
                       sx={{
                         "& .MuiPaginationItem-root": {
                           color: "var(--bs-primary1)",
                           borderRadius: "8px",
                         },
                         "& .MuiPaginationItem-root.Mui-selected": {
                           backgroundColor: "var(--bs-primary1)",
                           color: "#fff",
                           fontWeight: "bold",
                           "&:hover": {
                             backgroundColor: "var(--bs-primary1)",
                           },
                         },
                       }}
                     />
                   </Box>
                 </Box>
               </Paper>

        
   </>
  )
}

export default PayInWalletReport